.courses-container {
  min-height: 100vh;
  background-color: #f5f7fa;
  padding: 40px;
  font-family: 'Segoe UI', sans-serif;
}

.courses-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.courses-header h1 {
  font-size: 2rem;
  color: #333;
}

.back-btn {
  background-color: transparent;
  border: 2px solid #2575fc;
  color: #2575fc;
  padding: 10px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.back-btn:hover {
  background-color: #2575fc;
  color: white;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
}

.course-card {
  background-color: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease;
}

.course-card:hover {
  transform: translateY(-5px);
}

.course-card h2 {
  color: #2575fc;
  margin-bottom: 10px;
}

.course-card p {
  color: #555;
  margin-bottom: 20px;
}

.access-btn {
  background-color: #2575fc;
  color: white;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.access-btn:hover {
  background-color: #1a52d8;
}

/* src/pages/Aluno/CursoDetalhes.css */
.curso-detalhes {
  padding: 2rem;
  max-width: 700px;
  margin: auto;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
}

.curso-detalhes h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.back-btn {
  margin-bottom: 1rem;
  background: #eee;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 4px;
}
